'use client';

import { useState } from 'react';
import { motion } from 'framer-motion';
import { Volume2, Monitor, Keyboard, Mouse, Globe, Shield, Bell, Palette, Download, Upload, Trash2, Music, Zap, Activity } from 'lucide-react';

export default function CyberWorldSettingsPage() {
  const [settings, setSettings] = useState({
    audio: {
      masterVolume: 75,
      musicVolume: 60,
      sfxVolume: 80,
      ambientVolume: 40,
      muteOnFocusLoss: false
    },
    graphics: {
      quality: 'high',
      shadows: true,
      particles: true,
      antiAliasing: true,
      bloom: true,
      fpsLimit: 60
    },
    controls: {
      movementSpeed: 100,
      cameraSensitivity: 50,
      keybindings: {
        moveUp: 'W',
        moveDown: 'S',
        moveLeft: 'A',
        moveRight: 'D',
        interact: 'E',
        chat: 'Enter',
        inventory: 'I',
        quests: 'Q',
        map: 'M'
      }
    },
    account: {
      username: 'Agent_Unknown',
      email: 'agent@cyberworld.net',
      autoLogin: true,
      rememberPassword: false,
      twoFactorAuth: false
    },
    privacy: {
      showOnlineStatus: true,
      allowFriendRequests: true,
      allowPrivateMessages: true,
      showLocation: true,
      dataCollection: false
    }
  });

  const updateSetting = (category: string, key: string, value: any) => {
    setSettings(prev => ({
      ...prev,
      [category]: {
        ...(prev[category as keyof typeof prev] || {}),
        [key]: value
      }
    }));
  };

  const categories = [
    { id: 'audio', label: 'Audio', icon: Volume2, color: '#00f3ff' },
    { id: 'graphics', label: 'Graphics', icon: Monitor, color: '#00ff41' },
    { id: 'controls', label: 'Controls', icon: Keyboard, color: '#ff00ff' },
    { id: 'account', label: 'Account', icon: Shield, color: '#ffaa00' },
    { id: 'privacy', label: 'Privacy', icon: Globe, color: '#ffff00' }
  ];

  const [activeCategory, setActiveCategory] = useState('audio');

  return (
    <div className="min-h-screen bg-[#0a0a0a] text-white font-mono p-8">
      <div className="max-w-6xl mx-auto">
        {/* Header */}
        <motion.div
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          className="mb-8"
        >
          <h1 className="text-5xl text-[#00f3ff] mb-2 font-bold" style={{ textShadow: '0 0 30px rgba(0,243,255,0.8)' }}>
            ⚙️ CYBERWORLD SETTINGS
          </h1>
          <p className="text-[#00ff41] text-lg">Configure your CyberWorld experience</p>
        </motion.div>

        <div className="grid grid-cols-1 lg:grid-cols-4 gap-6">
          {/* Category Sidebar */}
          <div className="lg:col-span-1">
            <div className="bg-[rgba(10,10,10,0.95)] border-2 border-[#00d4ff] rounded-lg p-4">
              <div className="space-y-2">
                {categories.map((category) => {
                  const Icon = category.icon;
                  const isActive = activeCategory === category.id;
                  return (
                    <motion.button
                      key={category.id}
                      onClick={() => setActiveCategory(category.id)}
                      className={`w-full p-4 rounded-lg text-left flex items-center gap-3 transition-all ${
                        isActive
                          ? 'bg-[rgba(0,212,255,0.2)] border-2 border-[#00d4ff]'
                          : 'bg-[rgba(0,0,0,0.3)] border-2 border-transparent hover:border-[#00d4ff]/30'
                      }`}
                      whileHover={{ scale: 1.02 }}
                      whileTap={{ scale: 0.98 }}
                    >
                      <Icon className="w-5 h-5" style={{ color: category.color }} />
                      <span className="font-bold" style={{ color: isActive ? category.color : '#aaa' }}>
                        {category.label}
                      </span>
                    </motion.button>
                  );
                })}
              </div>
            </div>
          </div>

          {/* Settings Content */}
          <div className="lg:col-span-3">
            <motion.div
              key={activeCategory}
              initial={{ opacity: 0, x: 20 }}
              animate={{ opacity: 1, x: 0 }}
              className="bg-[rgba(10,10,10,0.95)] border-2 border-[#00d4ff] rounded-lg p-6"
            >
              {/* Audio Settings */}
              {activeCategory === 'audio' && (
                <div className="space-y-6">
                  <motion.div
                    initial={{ opacity: 0, y: -20 }}
                    animate={{ opacity: 1, y: 0 }}
                    className="mb-6"
                  >
                    <h2 className="text-3xl text-[#00f3ff] mb-2 font-bold flex items-center gap-3">
                      <Volume2 className="w-8 h-8" />
                      Audio Settings
                    </h2>
                    <p className="text-sm text-[#888]">Configure all audio output including music, sound effects, and ambient sounds</p>
                  </motion.div>
                  
                  <div className="space-y-6">
                    <div className="p-4 bg-[rgba(0,243,255,0.05)] border border-[#00f3ff]/30 rounded-lg">
                      <div className="flex justify-between mb-3">
                        <label className="text-[#00ff41] font-bold flex items-center gap-2">
                          <Volume2 className="w-4 h-4" />
                          Master Volume
                        </label>
                        <span className="text-[#00f3ff] font-mono font-bold">{settings.audio.masterVolume}%</span>
                      </div>
                      <input
                        type="range"
                        min="0"
                        max="100"
                        value={settings.audio.masterVolume}
                        onChange={(e) => updateSetting('audio', 'masterVolume', parseInt(e.target.value))}
                        className="w-full h-2 bg-[rgba(0,243,255,0.2)] rounded-lg appearance-none cursor-pointer accent-[#00f3ff]"
                      />
                      <div className="flex justify-between text-xs text-[#888] mt-1">
                        <span>Muted</span>
                        <span>Full Volume</span>
                      </div>
                    </div>

                    <div className="p-4 bg-[rgba(0,243,255,0.05)] border border-[#00f3ff]/30 rounded-lg">
                      <div className="flex justify-between mb-3">
                        <label className="text-[#00ff41] font-bold flex items-center gap-2">
                          <Music className="w-4 h-4" />
                          Music Volume
                        </label>
                        <span className="text-[#00f3ff] font-mono font-bold">{settings.audio.musicVolume}%</span>
                      </div>
                      <input
                        type="range"
                        min="0"
                        max="100"
                        value={settings.audio.musicVolume}
                        onChange={(e) => updateSetting('audio', 'musicVolume', parseInt(e.target.value))}
                        className="w-full h-2 bg-[rgba(0,243,255,0.2)] rounded-lg appearance-none cursor-pointer accent-[#00f3ff]"
                      />
                      <div className="text-xs text-[#888] mt-2">Background music and room themes</div>
                    </div>

                    <div className="p-4 bg-[rgba(0,243,255,0.05)] border border-[#00f3ff]/30 rounded-lg">
                      <div className="flex justify-between mb-3">
                        <label className="text-[#00ff41] font-bold flex items-center gap-2">
                          <Zap className="w-4 h-4" />
                          Sound Effects Volume
                        </label>
                        <span className="text-[#00f3ff] font-mono font-bold">{settings.audio.sfxVolume}%</span>
                      </div>
                      <input
                        type="range"
                        min="0"
                        max="100"
                        value={settings.audio.sfxVolume}
                        onChange={(e) => updateSetting('audio', 'sfxVolume', parseInt(e.target.value))}
                        className="w-full h-2 bg-[rgba(0,243,255,0.2)] rounded-lg appearance-none cursor-pointer accent-[#00f3ff]"
                      />
                      <div className="text-xs text-[#888] mt-2">Combat sounds, UI clicks, and action feedback</div>
                    </div>

                    <div className="p-4 bg-[rgba(0,243,255,0.05)] border border-[#00f3ff]/30 rounded-lg">
                      <div className="flex justify-between mb-3">
                        <label className="text-[#00ff41] font-bold flex items-center gap-2">
                          <Activity className="w-4 h-4" />
                          Ambient Volume
                        </label>
                        <span className="text-[#00f3ff] font-mono font-bold">{settings.audio.ambientVolume}%</span>
                      </div>
                      <input
                        type="range"
                        min="0"
                        max="100"
                        value={settings.audio.ambientVolume}
                        onChange={(e) => updateSetting('audio', 'ambientVolume', parseInt(e.target.value))}
                        className="w-full h-2 bg-[rgba(0,243,255,0.2)] rounded-lg appearance-none cursor-pointer accent-[#00f3ff]"
                      />
                      <div className="text-xs text-[#888] mt-2">Environmental sounds and ambient atmosphere</div>
                    </div>

                    <div className="flex items-center justify-between p-4 bg-[rgba(0,243,255,0.1)] rounded-lg">
                      <label className="text-[#00ff41] font-bold">Mute on Focus Loss</label>
                      <input
                        type="checkbox"
                        checked={settings.audio.muteOnFocusLoss}
                        onChange={(e) => updateSetting('audio', 'muteOnFocusLoss', e.target.checked)}
                        className="w-6 h-6 accent-[#00f3ff] cursor-pointer"
                      />
                    </div>
                  </div>
                </div>
              )}

              {/* Graphics Settings */}
              {activeCategory === 'graphics' && (
                <div className="space-y-6">
                  <h2 className="text-3xl text-[#00ff41] mb-6 font-bold flex items-center gap-3">
                    <Monitor className="w-8 h-8" />
                    Graphics Settings
                  </h2>
                  
                  <div className="space-y-6">
                    <div>
                      <label className="text-[#00ff41] font-bold block mb-3">Graphics Quality</label>
                      <select
                        value={settings.graphics.quality}
                        onChange={(e) => updateSetting('graphics', 'quality', e.target.value)}
                        className="w-full p-3 bg-[rgba(0,0,0,0.5)] border-2 border-[#00ff41] text-[#00ff41] rounded-lg font-mono"
                      >
                        <option value="low">Low</option>
                        <option value="medium">Medium</option>
                        <option value="high">High</option>
                        <option value="ultra">Ultra</option>
                      </select>
                    </div>

                    <div className="p-4 bg-[rgba(0,255,65,0.05)] border border-[#00ff41]/30 rounded-lg">
                      <label className="text-[#00ff41] font-bold block mb-3 flex items-center gap-2">
                        <Activity className="w-4 h-4" />
                        FPS Limit
                      </label>
                      <select
                        value={settings.graphics.fpsLimit}
                        onChange={(e) => updateSetting('graphics', 'fpsLimit', parseInt(e.target.value))}
                        className="w-full p-3 bg-[rgba(0,0,0,0.5)] border-2 border-[#00ff41] text-[#00ff41] rounded-lg font-mono hover:border-[#00ff41] hover:shadow-[0_0_15px_rgba(0,255,65,0.5)] transition-all"
                      >
                        <option value="30">30 FPS - Power Saving</option>
                        <option value="60">60 FPS - Standard</option>
                        <option value="120">120 FPS - High Refresh</option>
                        <option value="144">144 FPS - Maximum Refresh</option>
                        <option value="0">Unlimited - No Cap</option>
                      </select>
                      <div className="text-xs text-[#888] mt-2">
                        {settings.graphics.fpsLimit === 0 
                          ? 'Frame rate will match your display refresh rate' 
                          : `Caps frame rate at ${settings.graphics.fpsLimit} FPS for consistent performance`}
                      </div>
                    </div>

                    <div className="space-y-4">
                      {[
                        { key: 'shadows', label: 'Enable Shadows', desc: 'Dynamic shadows for buildings, NPCs, and objects', icon: Shield },
                        { key: 'particles', label: 'Enable Particles', desc: 'Ambient effects, data streams, and visual particles', icon: Zap },
                        { key: 'antiAliasing', label: 'Anti-Aliasing', desc: 'Smoother edges and reduced pixelation', icon: Monitor },
                        { key: 'bloom', label: 'Bloom Effects', desc: 'Glowing neon effects and light halos', icon: Zap }
                      ].map(({ key, label, desc, icon: Icon }) => (
                        <motion.div
                          key={key}
                          initial={{ opacity: 0, x: -10 }}
                          animate={{ opacity: 1, x: 0 }}
                          transition={{ delay: 0.5 }}
                          className="flex items-center justify-between p-4 bg-[rgba(0,255,65,0.1)] rounded-lg border-l-4 border-[#00ff41] hover:bg-[rgba(0,255,65,0.15)] transition-all cursor-pointer"
                          whileHover={{ scale: 1.02, x: 5 }}
                        >
                          <div className="flex items-center gap-3 flex-1">
                            <Icon className="w-5 h-5 text-[#00ff41]" />
                            <div className="flex-1">
                              <label className="text-[#00ff41] font-bold block">{label}</label>
                              <div className="text-xs text-[#888] mt-1">{desc}</div>
                            </div>
                          </div>
                          <input
                            type="checkbox"
                            checked={settings.graphics[key as keyof typeof settings.graphics] as boolean}
                            onChange={(e) => updateSetting('graphics', key, e.target.checked)}
                            className="w-6 h-6 accent-[#00ff41] cursor-pointer"
                          />
                        </motion.div>
                      ))}
                    </div>
                    
                    {/* Graphics Performance Info */}
                    <div className="p-4 bg-[rgba(0,255,65,0.05)] rounded-lg border border-[#00ff41]/20 mt-4">
                      <div className="text-[#00ff41] font-bold mb-3 flex items-center gap-2">
                        <Monitor className="w-4 h-4" />
                        Performance Impact
                      </div>
                      <div className="grid grid-cols-2 gap-3 text-xs">
                        <div>
                          <div className="text-[#888] mb-1">Current Settings:</div>
                          <div className="text-[#00ff41] font-bold">
                            {settings.graphics.quality.charAt(0).toUpperCase() + settings.graphics.quality.slice(1)} Quality
                          </div>
                        </div>
                        <div>
                          <div className="text-[#888] mb-1">Estimated FPS:</div>
                          <div className="text-[#00d4ff] font-bold">
                            {settings.graphics.quality === 'low' ? '120+ FPS' :
                             settings.graphics.quality === 'medium' ? '90-120 FPS' :
                             settings.graphics.quality === 'high' ? '60-90 FPS' :
                             '30-60 FPS'}
                          </div>
                        </div>
                        <div>
                          <div className="text-[#888] mb-1">GPU Usage:</div>
                          <div className="text-[#ffff00] font-bold">
                            {settings.graphics.quality === 'low' ? 'Low (20-30%)' :
                             settings.graphics.quality === 'medium' ? 'Moderate (40-50%)' :
                             settings.graphics.quality === 'high' ? 'High (60-70%)' :
                             'Very High (80-90%)'}
                          </div>
                        </div>
                        <div>
                          <div className="text-[#888] mb-1">Visual Quality:</div>
                          <div className="text-[#ff00ff] font-bold">
                            {settings.graphics.quality === 'low' ? 'Basic' :
                             settings.graphics.quality === 'medium' ? 'Good' :
                             settings.graphics.quality === 'high' ? 'Excellent' :
                             'Maximum'}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              )}

              {/* Controls Settings */}
              {activeCategory === 'controls' && (
                <div className="space-y-6">
                  <h2 className="text-3xl text-[#ff00ff] mb-6 font-bold flex items-center gap-3">
                    <Keyboard className="w-8 h-8" />
                    Controls Settings
                  </h2>
                  
                  <div className="space-y-6">
                    <div>
                      <div className="flex justify-between mb-3">
                        <label className="text-[#00ff41] font-bold">Movement Speed</label>
                        <span className="text-[#ff00ff]">{settings.controls.movementSpeed}%</span>
                      </div>
                      <input
                        type="range"
                        min="50"
                        max="200"
                        value={settings.controls.movementSpeed}
                        onChange={(e) => updateSetting('controls', 'movementSpeed', parseInt(e.target.value))}
                        className="w-full h-2 bg-[rgba(255,0,255,0.2)] rounded-lg appearance-none cursor-pointer accent-[#ff00ff]"
                      />
                    </div>

                    <div>
                      <div className="flex justify-between mb-3">
                        <label className="text-[#00ff41] font-bold">Camera Sensitivity</label>
                        <span className="text-[#ff00ff]">{settings.controls.cameraSensitivity}%</span>
                      </div>
                      <input
                        type="range"
                        min="0"
                        max="100"
                        value={settings.controls.cameraSensitivity}
                        onChange={(e) => updateSetting('controls', 'cameraSensitivity', parseInt(e.target.value))}
                        className="w-full h-2 bg-[rgba(255,0,255,0.2)] rounded-lg appearance-none cursor-pointer accent-[#ff00ff]"
                      />
                    </div>

                    <div className="space-y-4">
                      <h3 className="text-xl text-[#ff00ff] font-bold mb-4">Key Bindings</h3>
                      {Object.entries(settings.controls.keybindings).map(([action, key]) => (
                        <div key={action} className="flex items-center justify-between p-4 bg-[rgba(255,0,255,0.1)] rounded-lg">
                          <span className="text-[#00ff41] font-bold capitalize">
                            {action.replace(/([A-Z])/g, ' $1').trim()}
                          </span>
                          <button className="px-4 py-2 bg-[rgba(255,0,255,0.2)] border-2 border-[#ff00ff] text-[#ff00ff] rounded font-mono font-bold hover:bg-[rgba(255,0,255,0.3)] transition-all">
                            {key}
                          </button>
                        </div>
                      ))}
                    </div>
                  </div>
                </div>
              )}

              {/* Account Settings */}
              {activeCategory === 'account' && (
                <div className="space-y-6">
                  <h2 className="text-3xl text-[#ffaa00] mb-6 font-bold flex items-center gap-3">
                    <Shield className="w-8 h-8" />
                    Account Settings
                  </h2>
                  
                  <div className="space-y-6">
                    <div>
                      <label className="text-[#00ff41] font-bold block mb-2">Username</label>
                      <input
                        type="text"
                        value={settings.account.username}
                        onChange={(e) => updateSetting('account', 'username', e.target.value)}
                        className="w-full p-3 bg-[rgba(0,0,0,0.5)] border-2 border-[#ffaa00] text-[#00ff41] rounded-lg font-mono"
                      />
                    </div>

                    <div>
                      <label className="text-[#00ff41] font-bold block mb-2">Email</label>
                      <input
                        type="email"
                        value={settings.account.email}
                        onChange={(e) => updateSetting('account', 'email', e.target.value)}
                        className="w-full p-3 bg-[rgba(0,0,0,0.5)] border-2 border-[#ffaa00] text-[#00ff41] rounded-lg font-mono"
                      />
                    </div>

                    <div className="space-y-4">
                      {[
                        { key: 'autoLogin', label: 'Auto-Login on Launch' },
                        { key: 'rememberPassword', label: 'Remember Password' },
                        { key: 'twoFactorAuth', label: 'Two-Factor Authentication' }
                      ].map(({ key, label }) => (
                        <div key={key} className="flex items-center justify-between p-4 bg-[rgba(255,170,0,0.1)] rounded-lg">
                          <label className="text-[#00ff41] font-bold">{label}</label>
                          <input
                            type="checkbox"
                            checked={settings.account[key as keyof typeof settings.account] as boolean}
                            onChange={(e) => updateSetting('account', key, e.target.checked)}
                            className="w-6 h-6 accent-[#ffaa00] cursor-pointer"
                          />
                        </div>
                      ))}
                    </div>

                    <div className="flex gap-4 pt-4">
                      <button className="flex-1 px-6 py-3 bg-[rgba(0,255,65,0.2)] border-2 border-[#00ff41] text-[#00ff41] rounded-lg font-bold hover:bg-[rgba(0,255,65,0.3)] transition-all flex items-center justify-center gap-2">
                        <Download className="w-5 h-5" />
                        Export Data
                      </button>
                      <button className="flex-1 px-6 py-3 bg-[rgba(255,0,0,0.2)] border-2 border-[#ff0000] text-[#ff0000] rounded-lg font-bold hover:bg-[rgba(255,0,0,0.3)] transition-all flex items-center justify-center gap-2">
                        <Trash2 className="w-5 h-5" />
                        Delete Account
                      </button>
                    </div>
                  </div>
                </div>
              )}

              {/* Privacy Settings */}
              {activeCategory === 'privacy' && (
                <div className="space-y-6">
                  <h2 className="text-3xl text-[#ffff00] mb-6 font-bold flex items-center gap-3">
                    <Globe className="w-8 h-8" />
                    Privacy Settings
                  </h2>
                  
                  <div className="space-y-4">
                    {[
                      { key: 'showOnlineStatus', label: 'Show Online Status', description: 'Allow other players to see when you are online' },
                      { key: 'allowFriendRequests', label: 'Allow Friend Requests', description: 'Let other players send you friend requests' },
                      { key: 'allowPrivateMessages', label: 'Allow Private Messages', description: 'Allow players to send you private messages' },
                      { key: 'showLocation', label: 'Show Current Location', description: 'Display your current room location to friends' },
                      { key: 'dataCollection', label: 'Allow Data Collection', description: 'Help improve the game by sharing anonymous usage data' }
                    ].map(({ key, label, description }) => (
                      <div key={key} className="p-4 bg-[rgba(255,255,0,0.1)] rounded-lg border-l-4 border-[#ffff00]">
                        <div className="flex items-center justify-between mb-2">
                          <label className="text-[#00ff41] font-bold">{label}</label>
                          <input
                            type="checkbox"
                            checked={settings.privacy[key as keyof typeof settings.privacy] as boolean}
                            onChange={(e) => updateSetting('privacy', key, e.target.checked)}
                            className="w-6 h-6 accent-[#ffff00] cursor-pointer"
                          />
                        </div>
                        <p className="text-sm text-[#888]">{description}</p>
                      </div>
                    ))}
                  </div>
                </div>
              )}

              {/* Save Button */}
              <div className="mt-8 pt-6 border-t border-[#00d4ff]/30">
                <div className="flex gap-4">
                  <button className="flex-1 px-6 py-3 bg-[rgba(0,255,65,0.2)] border-2 border-[#00ff41] text-[#00ff41] rounded-lg font-bold hover:bg-[rgba(0,255,65,0.3)] hover:shadow-[0_0_20px_rgba(0,255,65,0.5)] transition-all">
                    Save Settings
                  </button>
                  <button className="px-6 py-3 bg-[rgba(0,212,255,0.2)] border-2 border-[#00d4ff] text-[#00d4ff] rounded-lg font-bold hover:bg-[rgba(0,212,255,0.3)] transition-all">
                    Reset to Defaults
                  </button>
                </div>
              </div>
            </motion.div>
          </div>
        </div>
      </div>
    </div>
  );
}
